<template>
    <div id="myLottie" class="container-fluid">
     <lottie :options="defaultOptions" :height="300"  v-on:animCreated="handleAnimation"/>
            <div class="container">
              
              <input class="mySlider" autocomplete="off" type="range" value="valor" min="0" max="3" step="1"
                   v-on:change="onSpeedChange" v-model="animationSpeed">
            </div>

          <div class="row">
            <div class="col">Not at all true</div>
            <div class="col"></div>
            <div class="col">Slightly true</div>
            <div class="col"></div>
            <div class="col">Often true</div>
            <div class="col"></div>
            <div class="col">Very true</div>
            <!--<vue-slider ref="slider" v-bind="options" v-on:change="onSpeedChange" v-model="value"></vue-slider>-->
          </div>
          <br>
          <div><button type="button" v-on:click="onNext" class="btn btn-primary">Next question</button></div>
    </div>
</template>

<script>
  import Lottie from 'vue-lottie';
  import vueSlider from 'vue-slider-component'
  import * as animationData from '../assets/data.json';
 
  export default {
    name: 'myLottie',
    components: {
       vueSlider,
      'lottie': Lottie
    },
    props: ['valor'],
    data: function () {
      return {
        defaultOptions: {animationData: animationData.default},
        animationSpeed: this.valor,
      options: {
        data: [
          "Not at all true",
          "Slightly true",
          "Often true",
          "Very true",
        ],
        eventType: 'auto',
        width: 'auto',
        height: 6,
        dotSize: 16,
        dotHeight: null,
        dotWidth: null,
        min: 0,
        max: 4,
        interval: 1,
        show: true,
        speed: 0.5,
        disabled: false,
        piecewise: true,
        usdKeyboard: false,
        enableCross: true,
        piecewiseStyle: {
          "backgroundColor": "#ccc",
          "visibility": "visible",
          "width": "12px",
          "height": "12px"
        },
        piecewiseLabel: true,
        tooltip: null,
        tooltipDir: 'top',
        reverse: false,
        clickable: true,
        realTime: false,
        lazy: false,
        formatter: null,
        bgStyle: {
          "backgroundColor": "#3498db"
        },
        sliderStyle: null,
        processStyle: null,
        piecewiseActiveStyle: {
          "backgroundColor": "#3498db"
        },
        tooltipStyle: null,
        labelStyle: null,
        labelActiveStyle: {
          "color": "#3498db" 
        }
      }
    }
    },
    watch: { 
      valor:{ 
          handler(newVal, oldVal) { // watch it
          console.log(this.valor);
          console.log('Prop changed: ', newVal, ' | was: ', oldVal)
        }
      }
    },
    methods: {
      handleAnimation: function (anim) {
        this.anim = anim;
        this.anim.playSegments([0,89, true]);
      },
 
      stop: function () {
        this.anim.stop();
      },
 
      play: function () {
        this.anim.play();
      },
 
      pause: function () {
        this.anim.pause();
      },
      onNext: function() {
        var next = true;
        this.animationSpeed = 0;
        this.$emit("next", next);
      },
      onSpeedChange: function () {
        //this.anim.setSpeed(this.animationSpeed);
        //console.log(this.animationSpeed);
        var answer = this.animationSpeed;
        if(this.animationSpeed === '0'){
          this.anim.playSegments([0,89, true]);
        }

        if(this.animationSpeed === '1'){
          this.anim.playSegments([90,179, true]);
        }

        if(this.animationSpeed === '2'){
          this.anim.playSegments([180,269, true]);
        }

        if(this.animationSpeed === '3'){
          this.anim.playSegments([270,359, true]);
        }
        this.$emit("answer", answer);
      }
    }
  }
</script>
